"use client"

import { useState, useEffect, use } from "react"
import { useRouter } from 'next/navigation'
import Image from "next/image"
import { ArrowLeft, Save, DollarSign, Hash, Package } from 'lucide-react'
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { useToast } from "@/hooks/use-toast"
import { api, Product } from "@/lib/api"
import AuthGuard from "@/components/auth-guard"

export default function EditProductPage({
  params,
}: {
  params: Promise<{ id: string }>
}) {
  const { id } = use(params)
  const router = useRouter()
  const { toast } = useToast()
  const [product, setProduct] = useState<Product | null>(null)
  const [price, setPrice] = useState<string>("")
  const [quantity, setQuantity] = useState<string>("")
  const [isLoading, setIsLoading] = useState(true)
  const [isSaving, setIsSaving] = useState(false)

  useEffect(() => {
    fetchProduct()
  }, [id])

  const fetchProduct = async () => {
    try {
      const data = await api.getProduct(id)
      setProduct(data)
      setPrice((data.price_modified ?? data.price_predicted).toString())
      setQuantity(data.quantity.toString())
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to load product",
        variant: "destructive"
      })
      console.error('Fetch error:', error)
      router.push('/dashboard')
    } finally {
      setIsLoading(false)
    }
  }

  const handleSave = async () => {
    const priceNum = parseFloat(price)
    const quantityNum = parseInt(quantity)

    // Validation
    if (isNaN(priceNum) || priceNum <= 0) {
      toast({
        title: "Invalid Price",
        description: "Please enter a valid price greater than 0",
        variant: "destructive"
      })
      return
    }

    if (isNaN(quantityNum) || quantityNum <= 0) {
      toast({
        title: "Invalid Quantity",
        description: "Please enter a valid quantity greater than 0",
        variant: "destructive"
      })
      return
    }

    setIsSaving(true)

    try {
      // Note: api.updateProduct would need to be implemented in api.ts
      // For now, using fetch directly
      const token = localStorage.getItem('token')
      const response = await fetch(`http://localhost:8000/products/${id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          price_modified: priceNum,
          quantity: quantityNum,
        }),
      })

      if (!response.ok) throw new Error('Failed to update product')

      toast({
        title: "Changes Saved",
        description: "Product has been updated successfully",
      })

      router.push('/dashboard')
    } catch (error) {
      toast({
        title: "Save Failed",
        description: "Unable to save changes",
        variant: "destructive"
      })
      console.error('Update error:', error)
    } finally {
      setIsSaving(false)
    }
  }

  if (isLoading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center py-12 text-muted-foreground">
          Loading product...
        </div>
      </div>
    )
  }

  if (!product) {
    return null
  }

  return (
    <AuthGuard>
      <div className="container mx-auto px-4 py-8 max-w-4xl">
        <Button
          variant="ghost"
          onClick={() => router.push(`/products/${id}`)}
          className="mb-6"
        >
          <ArrowLeft className="mr-2 h-4 w-4" />
          Back to Product Details
        </Button>

        <div className="mb-8 text-center">
          <h1 className="text-4xl font-bold mb-3 text-balance">Edit Product</h1>
          <p className="text-muted-foreground text-lg text-pretty">
            Update pricing and inventory information
          </p>
        </div>

        <div className="grid gap-6 lg:grid-cols-2">
          {/* Product Preview */}
          <Card className="border-2">
            <CardHeader>
              <CardTitle>Product Information</CardTitle>
              <CardDescription>
                Read-only product details
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="relative w-full aspect-square rounded-lg overflow-hidden border-2 border-border bg-muted">
                {product.has_image ? (
                  <img
                    src={`http://localhost:8000/products/${product.product_id}/image`}
                    alt={product.product_name}
                    className="object-cover w-full h-full"
                  />
                ) : (
                  <div className="flex items-center justify-center h-full bg-muted text-muted-foreground">
                    <Package className="h-16 w-16" />
                  </div>
                )}
              </div>

              <div className="space-y-3">
                <div className="flex flex-col gap-1.5 p-3 rounded-lg bg-muted">
                  <span className="text-xs font-medium text-muted-foreground">Product Name</span>
                  <span className="font-semibold">{product.product_name}</span>
                </div>

                <div className="flex flex-col gap-1.5 p-3 rounded-lg bg-muted">
                  <span className="text-xs font-medium text-muted-foreground">Product Type</span>
                  <span className="font-semibold">{product.product_type}</span>
                </div>

                <div className="flex flex-col gap-1.5 p-3 rounded-lg bg-primary/10 border border-primary/20">
                  <span className="text-xs font-medium text-muted-foreground">AI Predicted Price</span>
                  <span className="text-xl font-bold text-primary">
                    ${product.price_predicted.toFixed(2)}
                  </span>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Edit Form */}
          <Card className="border-2">
            <CardHeader>
              <CardTitle>Editable Fields</CardTitle>
              <CardDescription>
                Modify price and quantity
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="space-y-2">
                <Label htmlFor="price" className="flex items-center gap-2">
                  <DollarSign className="h-4 w-4 text-muted-foreground" />
                  Price
                </Label>
                <Input
                  id="price"
                  type="number"
                  step="0.01"
                  min="0"
                  placeholder="0.00"
                  value={price}
                  onChange={(e) => setPrice(e.target.value)}
                  className="text-lg"
                />
                <p className="text-xs text-muted-foreground">
                  Current price per unit
                </p>
              </div>

              <div className="space-y-2">
                <Label htmlFor="quantity" className="flex items-center gap-2">
                  <Hash className="h-4 w-4 text-muted-foreground" />
                  Quantity
                </Label>
                <Input
                  id="quantity"
                  type="number"
                  min="1"
                  placeholder="0"
                  value={quantity}
                  onChange={(e) => setQuantity(e.target.value)}
                  className="text-lg"
                />
                <p className="text-xs text-muted-foreground">
                  Total units in stock
                </p>
              </div>

              <div className="pt-4 border-t border-border">
                <div className="flex items-center justify-between text-sm mb-4">
                  <span className="text-muted-foreground">Estimated Total Value</span>
                  <span className="text-xl font-bold text-primary">
                    ${((parseFloat(price) || 0) * (parseInt(quantity) || 0)).toFixed(2)}
                  </span>
                </div>

                <div className="space-y-3">
                  <Button
                    onClick={handleSave}
                    disabled={isSaving || !price || !quantity}
                    size="lg"
                    className="w-full"
                  >
                    <Save className="mr-2 h-4 w-4" />
                    {isSaving ? 'Saving Changes...' : 'Save Changes'}
                  </Button>

                  <Button
                    onClick={() => router.push(`/products/${id}`)}
                    variant="outline"
                    size="lg"
                    className="w-full"
                  >
                    Cancel
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </AuthGuard>
  )
}
